import ImageCropper from '@/components/ling-imgcropper/ling-imgcropper.vue';
const app = getApp().globalData;
const { navBar } = app;
export default {
	components: {
		ImageCropper
	},
	data() {
		return {
			tempFilePath: '',
			cropFilePath: '',
			showtitle: true,
			navBar: navBar
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		bindPhonePage(){
			uni.navigateTo({
				url:'bindphone'
			})
		},
		upload() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: res => {
					this.showtitle = false;
					this.tempFilePath = res.tempFilePaths.shift();
				}
			});
		},
		confirm(e) {
			this.showtitle = true;
			this.tempFilePath = '';
			this.cropFilePath = e.detail.tempFilePath;
		},
		cancel() {
			this.showtitle = true;
		}
	}
};
